function Preview(props) {
  return (
    <div className="markdown-body" id="preview" dangerouslySetInnerHTML={{__html: marked(props.markdown)}}></div>
  );
}

function Editor(props) {
  return (
    <textarea 
      type="text"
      id="editor" 
      value={props.markdown} 
      onChange={props.onChange}>
    </textarea>
  );
}

function PreviewToolbar(props) {
  return (
    <div className="toolbar toolbar-preview">
      <h2 className="toolbar-header">Preview</h2>
      <i className={props.resizeIcon} onClick={props.onResize}></i>
    </div>
  );
}

function EditorToolbar(props) {
  return (
    <div className="toolbar toolbar-editor">
      <h2 className="toolbar-header">Editor</h2>
      <i className="clear fas fa-sync-alt" onClick={props.onClear} title="clear editor"></i>
      <i className={props.resizeIcon} onClick={props.onResize}></i>
    </div>
  )
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      markdown: localStorage.getItem('markdown') || placeholder,
      isEditorMaximized: false,
      isPreviewMaximixed: false
    };
    this.updateLocalStorage = this.updateLocalStorage.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleClear = this.handleClear.bind(this);
    this.handleEditorResize = this.handleEditorResize.bind(this);
    this.handlePreviewResize = this.handlePreviewResize.bind(this);
  }

  updateLocalStorage() {
    localStorage.setItem('markdown', this.state.markdown);
  }

  handleChange(e) {
    this.setState({markdown: e.target.value});
    setTimeout(this.updateLocalStorage, 10);
  }

  handleClear(e) {
    this.setState({markdown: ''});
    setTimeout(this.updateLocalStorage, 10);
  }

  handleEditorResize(e) {
    this.setState({isEditorMaximized: !this.state.isEditorMaximized});
    console.log(this.state.isEditorMaximized)
  }

  handlePreviewResize(e) {
    this.setState({isPreviewMaximixed: !this.state.isPreviewMaximixed});
    console.log(this.state.isPreviewMaximixed)
  }

  render() {
    const classes = this.state.isEditorMaximized ? 
      // if editor is maximixed
      ['editorWrap maximized',
      'previewWrap hidden',
      'fas fa-compress-arrows-alt'] :
      // if preview is maximized
      this.state.isPreviewMaximixed ? 
      ['editorWrap hidden',
      'previewWrap maximized',
      'fas fa-compress-arrows-alt'] :
      // default style
      ['editorWrap',
      'previewWrap',
      'fas fa-expand-arrows-alt'];

    return (
      <main>
        <div className={classes[0]}>
          <EditorToolbar 
            onClear={this.handleClear} 
            resizeIcon={classes[2]} 
            onResize={this.handleEditorResize} />
          <Editor 
            markdown={this.state.markdown} 
            onChange={this.handleChange} />
        </div>
        <div className={classes[1]}>
          <PreviewToolbar 
            resizeIcon={classes[2]}
            onResize={this.handlePreviewResize} />
          <Preview markdown={this.state.markdown} />
        </div>      
      </main>    
    );
  }
}

ReactDOM.render(<App />, document.getElementById('root'));